.card {
    border: 4px rgb(99, 99, 99) solid;
    border-radius: 2px;
    width: 150px;
    height: 150px;
    margin: 1rem;
    position: relative;
    box-shadow: 4px 4px 10px rgb(56, 56, 56);
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    background:rgb(255, 255, 255);
  }
  
  .card > .img-container > img {
    height: 100%;
  }
  
  .card:hover {
    box-shadow: 8px 8px 20px rgb(56, 56, 56);
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
  }
  
  .card:active {
    -webkit-transform: translate(1px, 1px);
            transform: translate(1px, 1px);
  }
nav {
    width: 100%;
    font-size: 3rem;
    color: #fff;
    background-color: rgb(56, 56, 56);
    height: auto;
    font-family: 'Bungee', cursive;
    opacity: 0.8;
    -webkit-text-stroke: 1px black;
  }
  
  a {
    color: #fed420;
  }
  
  a:hover {
    color: #fed420;
    text-decoration: none;
  }
  
  nav li {
    display: inline-block;
    padding: 26px 0;
    width: 25%;
    -webkit-text-stroke: 1px black;
    /* text-align: center; */
  }
  
  ul {
    /* padding-left: 0px; */
    display: flex;
    align-items: center;
  }
  
  @media only screen and (max-width: 750px) {
    #cur-sco, #top-sco {
      font-size: 2rem;
    }
  }
.wrapper {
    background: rgb(175, 175, 175);
    background-image: url(/clicky-click-click/static/media/drago.dd2a8975.jpg);
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
    padding-bottom: 50px;
    background-repeat: no-repeat;
    background-size: cover;  
  }
.title {
    width: 100%;
    text-align: right;
    font-size: 2.5rem;
    color: #fed420;
    padding-left: 3%;
    padding-right: 3%;  
    -webkit-text-stroke: 1px black;
  }
.title {
  width: 100%;
  text-align: center;
}
* {
  box-sizing: border-box;
  font-family: 'Bungee', cursive;
}

html, body, #root {
